var _this;
export default {
	name: "nav-bar",
	data() {
		return {
			// isFixed: false, //当滚动条高度大于152时是否定位
			// scrollHeight: 320,
			navbarInitTop: 0,
			isFixedTop: false,
			// stillFixe:false,
			// lastScorlH:0,
			topArr: ['学院介绍', '课程列表', '讲师介绍'],
			collegeId: '',
			myData: ['1', '2'],
			myDataDic: Object,
			teacherData: [],
			itemSelectIndex: 1,
			baseurl: "http://39.105.48.243:8080/crlink/",

		};
	},

	onShow() {
		console.log('college show');
		this.getListData()
		this.getTeacherData() 
	},

	onLoad(e) {

		//获取数据
		_this = this;
		_this.collegeId = e.collegeId;
	


	},
	mounted() {
		this.getTopViewHeight();
	},
	methods: {

		//获取列表数据
		getListData() {
			var loginkey = uni.getStorageSync('loginKey');
			this.$api.post('college!ajaxGetCollegeDetailNew.action', {
				collegeId: _this.collegeId,
				loginKey: loginkey
			}).then(res => {
				if (res.res.status == 0) {
					_this.myData = res.inf.arr;
					_this.myDataDic = res.inf;
				} else {
					uni.showToast({
						title: res.res.errMsg
					})
				}
			})
		},

		//获取老师数据
		getTeacherData() {
			var loginkey = uni.getStorageSync('loginKey');
			this.$api.post('college!ajaxGetCollegeTeacherIntro.action', {
				collegeId: _this.collegeId,
				loginKey: loginkey
			}).then(res => {
				if (res.res.status == 0) {
					_this.teacherData = res.inf.arr;
				} else {
					uni.showToast({
						title: res.res.errMsg
					})
				}
			})
		},

		didselectItem(item) {
			uni.navigateTo({
				url: './videoDetail?courseID=' + item.courseId + '&&collegeId=' + this.collegeId,
				success() {

				}
			})
		},

		//跳转购买页面
		tobuy() {
			if (_this.myDataDic.isBuy !== 1) {
				uni.navigateTo({
					url: './payView/payView?collegeId=' + _this.collegeId,
					success() {

					}
				})
			}


		},
		//顶部选项卡选择
		topItemSelect(e) {
			this.itemSelectIndex = e;
			// if(this.isFixedTop==true){
			// 	this.stillFixe = true;
			// }
		},
		//获取节点距离顶部的距离
		getTopViewHeight() {
			var that = this;
			uni.createSelectorQuery().select('#navbar').boundingClientRect(function(rect) {
				if (rect && rect.top > 0) {
					var navbarInitTop = parseInt(rect.top);
					that.navbarInitTop = navbarInitTop
				}
			}).exec();
		},

		comments() {
			//评价
			uni.navigateTo({
				url: './VdoDetail/VideoDetailcomment?collegeId=' + this.collegeId
			})
		},

		collection() {
			var loginkey = uni.getStorageSync('loginKey');
			this.$api.post('college!ajaxCollectCollege.action', {
				id: _this.collegeId,
				loginKey: loginkey
			}).then(res => {
				if (res.res.status == 0) {
					_this.myDataDic.isCollect = res.inf.isColl == 1 ? 0 : 1

					uni.showToast({
						title: res.inf.isColl == 1 ? '取消收藏' : '收藏成功',
						duration: 500
					})
				} else {
					uni.showToast({
						title: res.res.errMsg
					})
				}
			})
		}



	},
	

	/**
	 * 监听页面滑动事件
	 */
	onPageScroll: function(e) {
		var that = this;
		var scrollTop = parseInt(e.scrollTop); //滚动条距离顶部高度
		// console.log("scrollTop =" + scrollTop);
		// that.lastScorlH = scrollTop;
		//判断'滚动条'滚动的距离 和 '元素在初始时'距顶部的距离进行判断
		var isSatisfy = scrollTop >= that.navbarInitTop ? true : false;
		//为了防止不停的setData, 这儿做了一个等式判断。 只有处于吸顶的临界值才会不相等
		if (that.isFixedTop === isSatisfy) {
			return false;
		}
		that.isFixedTop = isSatisfy

	},
	filters: {
		formatRichText(html) { //控制小程序中图片大小
			if (html) {
				let newContent = html.replace(/<img[^>]*>/gi, function(match, capture) {
					// match = match.replace(/src="/g, 'src="http://39.105.48.243:8080/crlink');
					match = match.replace(/style="[^"]+"/gi, '').replace(/style='[^']+'/gi, '');
					match = match.replace(/width="[^"]+"/gi, '').replace(/width='[^']+'/gi, '');
					match = match.replace(/height="[^"]+"/gi, '').replace(/height='[^']+'/gi, '');
					return match;
				});

				newContent = newContent.replace(/style="[^"]+"/gi, function(match, capture) {
					match = match.replace(/width:[^;]+;/gi, 'max-width:100%;').replace(/width:[^;]+;/gi, 'max-width:100%;');
					return match;
				});
				// newContent = newContent.replace(/<br[^>]*\/>/gi, '');
				newContent = newContent.replace(/\<img/gi,
					'<img style="max-width:100%;height:auto;display:inline-block;margin:10rpx auto;"');
				return newContent;
			}

		}
	}


};
